import { Path } from '@ali/4ever-cangjie';

export const LASER_POINTER_HIGHLIGHT = Symbol('laser-pointer-highlight');
export const LASER_POINTER_REMOVE_HIGHLIGHT = Symbol(
  'laser-pointer-remove-highlight',
);

export interface LaserPointerHightPayload {
  /** node path */
  path: Path;
}

export function highlight(payload: LaserPointerHightPayload) {
  return {
    type: LASER_POINTER_HIGHLIGHT,
    payload,
  };
}

export function removeHighlight() {
  return {
    type: LASER_POINTER_REMOVE_HIGHLIGHT,
    payload: {},
  };
}
